<template>
  <div class="registration-modal">
    <vue-modal :value="switcherQuestionParams">
      <p>Вы проживаете в городе Владивосток/Пригород?</p>
      <vue-switcher v-model="switcherQuestionParams" />
      <p v-if="switcherQuestionParams[1].isSwitch">
        Мы работам только во Владивостоке и в пригороде!
      </p>
    </vue-modal>
  </div>

  <div class="registration-page" v-if="switcherQuestionParams[0].isSwitch">
    <div class="registration-page__title">
      <div class="registration-page__titles">
        <h2
          @click="selectTypeRegistration(loginType)"
          :class="{ active_login_type: loginType === 'registration' }"
        >
          регистрация
        </h2>
        <h2
          :class="{ active_login_type: loginType === 'login' }"
          @click="selectTypeRegistration(loginType)"
        >
          вход
        </h2>
      </div>
    </div>

    <vue-form
      class="registration-page__form"
      v-if="loginType === 'registration'"
    >
      <div class="form--switcher__role">
        <label class="form--switcher__label">Кем хотите работать?</label>
        <vue-switcher v-model="switcherRoleParams" />
      </div>
      <div
        class="form--text__input"
        v-for="(input, index) in textInputs"
        :key="index"
      >
        <label class="form--text__label">{{
          textInputs[index].placeholder
        }}</label>
        <vue-text-input
          @input="setUpdatedInputText($event)"
          v-model="textInputs[index]"
          :property-name="getPropertyName(index)"
        />
      </div>

      <div class="form--number__input">
        <label class="form--number__label">Номер телефона</label>
        <vue-number-input
          :modelValue="registrationInfo.userPhone"
          :number-params="registrationInfo.userPhone"
          @input="setUserPhone($event, registrationInfo.userPhone, 'phone')"
        />
      </div>

      <div class="form--number__input">
        <label class="form--number__label">Номер whatsapp</label>
        <vue-number-input
          :modelValue="registrationInfo.userWhatsapp"
          @input="setUserPhone($event, registrationInfo.userWhatsapp, 'whts')"
          :number-params="registrationInfo.userWhatsapp"
        />
      </div>

      <div class="form--date__input">
        <label class="form--date__label">Дата рождения</label>
        <vue-number-input
          :modelValue="registrationInfo.userDate"
          :number-params="registrationInfo.userDate"
        />
      </div>

      <div class="form--text__input">
        <label class="form--counter__label">Город</label>
        <vue-text-input
          class="form--component__input"
          :modelValue="registrationInfo.userCounter"
          :disabled="true"
        />
        <label class="form--date__label-error" ref="warningLabel"
          >На данный момент мы работаем только в этом регионе!</label
        >
      </div>
    </vue-form>

    <div class="registration-page__widget" v-if="loginType === 'registration'">
      <vue-widget class="widget">
        <div class="widget__offer">
          <p>С <span @click="goToPage('offer')">офертой</span> ознакомлен</p>
          <vue-check-box
            :value="checkboxState"
            @change="updateStateCheckBox($event)"
          />
        </div>
        <vue-button-text :title="'Отправить'" />
      </vue-widget>
    </div>

    <vue-form v-if="loginType === 'login'" class="registration-page__form">
      <div class="form--number__input">
        <label class="form--number__label">Номер телефона</label>
        <vue-number-input
          :modelValue="loginInfo.userPhone"
          @input="setUserPhone($event, loginInfo.userPhone, 'phone')"
        />
      </div>
      <div class="form--number__input">
        <label class="form--number__label">СМС КОД</label>
        <vue-number-input
          :modelValue="loginInfo.userSMS"
          @input="loginInfo.userSMS.value.value = $event"
        />
      </div>
      <div
        class="form--button__post"
        v-if="loginInfo.userSMS.value.value === null"
      >
        <vue-button-text
          :class="{ confirm_state: refConfirmBtnState === true }"
          v-if="!isTimerActive"
          :title="postSmsCode"
          @click="sendSmsCode"
        />

        <vue-timer
          v-else
          :initialTime="10"
          @updatedIsTimerEnded="
            isTimerActive = !isTimerActive;
            hasTimerExpired = !hasTimerExpired;
          "
        />
      </div>
      <div
        class="form--button__confirm"
        v-if="loginInfo.userSMS.value.value !== null"
      >
        <vue-button-text
          :class="{ confirm_state: refConfirmBtnState === true }"
          :title="titleConfirmBtn"
          @click="confirmSms(loginInfo.userSMS.value.value)"
        />
      </div>
    </vue-form>
  </div>
</template>
<script lang="ts" setup>
// vue
import { useRoute, useRouter, LocationQuery } from "vue-router";
import { ref, computed, Ref } from "vue";
//components
import VueNumberInput from "@/components/Inputs/VueNumberInput.vue";
import VueForm from "@/components/Forms/VueForm.vue";
import VueTextInput from "@/components/Inputs/VueTextInput.vue";
import VueModal from "@/components/Modals/VueModal.vue";
import VueSwitcher from "@/components/Switchers/VueSwitcher.vue";
import VueWidget from "@/components/Widgets/VueWidget.vue";
import VueCheckBox from "@/components/CheckBoxs/VueCheckBox.vue";
import VueButtonText from "@/components/Buttons/VueButtonText.vue";
import VueTimer from "@/components/Timer/VueTimer.vue";
//interfaces and types
import { TitleParams } from "../interfaces/TitleParamsInterface";
import { RegistrationInfoType } from "@/types/RegistrationInfoType";
import { InputNumber } from "@/interfaces/InputNumberInterface";
import { SwitcherParamsType } from "@/types/SwitcherParamsType";

const loginType = ref("registration"); //для входа login

const selectTypeRegistration = (val: string) => {
  if (val === "registration") {
    loginType.value = "login";
  } else if (val === "login") {
    loginType.value = "registration";
  }
};

const switcherRoleParams = ref([
  {
    id: 1,
    value: "Грузчик",
    isSwitch: false,
  },
  {
    id: 2,
    value: "Грузоперевозчик",
    isSwitch: false,
  },
]);

const switcherQuestionParams = ref<SwitcherParamsType[]>([
  {
    id: 1,
    value: "Да",
    isSwitch: false,
  },
  {
    id: 2,
    value: "Нет",
    isSwitch: false,
  },
]);

const loginInfo = {
  userPhone: {
    value: ref(null),
    isValid: ref(null),
    type: "phone",
    placeholder: "+7 (###)-###-##-##",
    disabled: false,
  },
  userSMS: {
    value: ref(null),
    isValid: ref(null),
    placeholder: "1234",
    disabled: false,
  },
};

const isTimerActive = ref(false);
const hasTimerExpired = ref(false);

const postSmsCode = computed(() =>
  hasTimerExpired.value ? "Запросить код ещё раз!" : "Запросить код"
);

const sendSmsCode = () => {
  isTimerActive.value = true;
  hasTimerExpired.value = false;
};

const refConfirmBtnState = ref(false);

const titleConfirmBtn = ref("Подтвердить");

const confirmSms = (val: string | number) => {
  refConfirmBtnState.value = !refConfirmBtnState.value;
  titleConfirmBtn.value = "Проверка";
  setTimeout(() => {
    refConfirmBtnState.value = !refConfirmBtnState.value;
    titleConfirmBtn.value = "Подтвержден!";
    setTimeout(() => {
      router.push("/");
    }, 1000);
  }, 2000);
};

const router = useRouter();
const route = useRoute();

const setQueryParamsRegistration = (
  type: string,
  value: number | string | null
): void => {
  router.push({
    query: {
      ...router.currentRoute.value.query,
      [type]: value,
    },
  });
};

const checkboxState = ref(false);

const updateStateCheckBox = (ev: boolean) => {
  checkboxState.value = ev;
  router.push({
    query: {
      ...router.currentRoute.value.query,
      ch: checkboxState.value.toString(),
    },
  });
};

const registrationInfo: RegistrationInfoType = {
  userPhone: {
    value: ref(null),
    isValid: ref(null),
    type: "phone",
    setQueryParams: setQueryParamsRegistration,
    placeholder: "+7 (###)-###-##-##",
    disabled: false,
  },
  userWhatsapp: {
    value: ref(null),
    isValid: ref(null),
    type: "whts",
    setQueryParams: setQueryParamsRegistration,
    placeholder: "+7 (###)-###-##-##",
    disabled: false,
  },
  userName: {
    value: "",
    isValid: ref(null),
    type: "name",
    placeholder: "Имя",
  },
  userSerName: {
    value: "",
    isValid: ref(null),
    type: "sername",
    placeholder: "Фамилия",
  },
  userPatronymic: {
    value: "",
    isValid: ref(null),
    type: "patr",
    placeholder: "Отчество",
  },
  userDate: {
    value: ref(""),
    isValid: ref(null),
    type: "date",
    placeholder: "дд.мм.гггг",
    disabled: false,
  },
  userCounter: {
    value: "Владивосток, пригород",
    isValid: ref(null),
  },
};

//порядок имеет значение!
const textInputs = [
  registrationInfo.userSerName,
  registrationInfo.userName,
  registrationInfo.userPatronymic,
];

//порядок важен!
const getPropertyName = (index: number): string => {
  const properties = ["userName", "userSerName", "userPatronymic"];
  return properties[index];
};

const setUserPhone = (ev: any, info: InputNumber, type: string) => {
  info.value = ev;

  if (info.setQueryParams) {
    info.setQueryParams(type, info.value.value);
  }
};

const setUpdatedInputText = (ev: any) => {
  const evValue = ev.eventValue;
  const evType = ev.eventType;
  const evValid = ev.eventValid;
  const evName: keyof RegistrationInfoType = ev.eventName;

  registrationInfo[evName].value = evValue;
  registrationInfo[evName].isValid = evValid;

  setQueryParamsRegistration(evType, evValue);
};

const parseQueryParams = (
  query: LocationQuery,
  registrationInfo: RegistrationInfoType
) => {
  Object.keys(query).forEach((key) => {
    const foundKey = Object.keys(registrationInfo).find(
      (infoKey) =>
        registrationInfo[infoKey as keyof RegistrationInfoType].type === key
    ) as keyof RegistrationInfoType | undefined;

    if (foundKey) {
      registrationInfo[foundKey].value = query[key] as string;
    }
  });
};

const goToPage = (path: string) => {
  router.push({
    path: path, // Новый маршрут
    query: {
      ...router.currentRoute.value.query,
      reg: "true",
    },
  });
};

if (route.query) {
  const queryParams = route.query;

  //провека на наличие флага проживания клиента
  if (queryParams.root === "true") {
    switcherQuestionParams.value[0].isSwitch = true;
  } else if (queryParams.root === "false") {
    switcherQuestionParams.value[0].isSwitch = false;
  }
  parseQueryParams(queryParams, registrationInfo);

  if (route.query.ch) {
    if (route.query.ch === "true") {
      checkboxState.value = true;
    } else if (route.query.ch === "false") {
      checkboxState.value = false;
    }
  }
}

// const sendData = () => {};
</script>

